@mixin small-devices-and-up {
    @media screen and (min-width: #{$small-devices}) {
        @content
    }
}

@mixin medium-devices {
    @media screen and (min-width: #{$small-devices}) and (max-width: #{$large-devices}) {
        @content
    }
}
@mixin large-devices {
    @media screen and (min-width: #{$large-devices}) {
        @content
    }
}