.media-links {
    display: none;
    margin: 0;
    padding: 0;

    li {
        padding: 0 0 0 40px;
        margin: 0 0 10px 0;

        a {
            text-decoration: none;
            padding: 3px 10px;
            border-radius: 3px;
            @include transition-property(background-color);
            @include transition-duration(125ms);

            &:hover {
                background-color: #e5e5e5;
            }
        }
        &.linkedin{
            background : {
                image: url('../img/icons/linkedin.svg');
                repeat: no-repeat;
            }
        }
        &.github {
            background : {
                image: url('../img/icons/github.svg');
                repeat: no-repeat;
            }
        }
    }
    @include small-devices-and-up {
        display: block;
        list-style: none;
        margin: 20px 0 20px 0;
        opacity: 0;
        @include transition(opacity, 400ms, ease-out);
    }
}//media-links
[data-app-state="active"] {
    .media-links {
        opacity: 1;
        transition-delay: 800ms;
    }
}//data-app-state

[data-app-state="sleeping"] {
    .media-links {
        opacity: 0;
        transition-delay: 100ms;
    }
}//data-app-state