.modal {
    display: block;
    position: fixed;
    opacity: 0;
    top: 0;
    bottom: 0;
    width: 100%;
    padding: 0;
    background-color:rgba($color: #ffffff, $alpha: .93);
    pointer-events: none;
    z-index: 2;
    @include transition-property(opacity);
    @include transition-delay(300ms, 0ms);
    @include transition-duration(200ms);
    @include transition-timing-function(ease-in-out);

    .modal-content {
        display: none;
        overflow-y: auto;
        height: 100%;
        padding: 0;
        -webkit-overflow-scrolling: touch;

        figure {
            @include flexBox;
            @include flexAlignItems(center);
            @include flexDirection(column);
            @include flexJustifyContent(center);

            &.center {
                height: 100%;
                margin: 0;
                padding: 0;
            }

            img {
                max-width: 100%;
                margin: 0 0 3em 0;

                @include small-devices-and-up {
                    max-width: 50%;
                }
            }
            .drop-shadow {
                box-shadow: 1px 1px 25px rgba($color: #000000, $alpha: 0.10);  
            }//drop-shadow
        }
        .row {
            @include flexBox();
            @include flexJustifyContent(space-around);
            @include flexAlignItems(center);
            overflow: scroll;

            p {
                display: none;
            }

            img {
                width: 90%;

                @include small-devices-and-up {
                    width: 40%;
                }
            }
        }
    }

    &.is-active {
        display: block;
        opacity: 1;
        pointer-events: all;
        @include transition(opacity, 300ms, ease-in-out);

        .modal-content {
            display: block;
        }  
    }//is-active

    .button-close {
        top: 10px;
        right: 10px;
    }

    .modal-footer {
        display: none;
        position: absolute;
        right: 0;
        bottom: 0;
        left: 0;
        padding: 5px 15px 0 15px;
        background-color: white;
        text-align: center;
        -webkit-backdrop-filter: blur(10px);
        backdrop-filter: blur(10px);
        box-shadow: -1px 0px 9px rgba(0, 0, 0, 0.12);

        @include small-devices-and-up {
            background-color: transparent;
        }
    }

    @include small-devices-and-up {
        right: 0;
        padding: 70px 80px 0 80px;
        
        .button-close {
            top: 20px;
            right: 55px;
        }
    }
}
[data-app-state="sleeping"] {
    .modal {
        display: none;

        .modal-content {
            display: none;
        }
    }
}