@font-face {
  font-family: 'layericons';
  src:  url('../fonts/slr/layericons.eot?h7hxzz');
  src:  url('../fonts/slr/layericons.eot?h7hxzz#iefix') format('embedded-opentype'),
    url('../fonts/slr/layericons.ttf?h7hxzz') format('truetype'),
    url('../fonts/slr/layericons.woff?h7hxzz') format('woff'),
    url('../fonts/slr/layericons.svg?h7hxzz#layericons') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="slr-"], [class*=" slr-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'layericons' !important;
  // speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

