.card {
    display: block;
    position: relative;
    background-color: white;
    border-radius: 8px;
    width: 100%;
    height: auto;
    margin: 30px;
    opacity: 1;
    overflow: hidden;
    box-shadow: 1px 1px 2px 1px rgba($color: #000000, $alpha: 0.1);
    @include transition-property(box-shadow);
    @include transition-delay(80ms);
    @include transition-duration(200ms);

    @include small-devices-and-up {
        width: 300px;
        height: 350px;
        overflow: hidden;
    }

    img {
        @include transition-property(all);
        @include transition-delay(100ms);
        @include transition-duration(200ms);
        @include transition-timing-function(ease-in-out);
    }

    &:hover {
        cursor: pointer;
        border-radius: 8px;
        @include transition-property(box-shadow, transform);
        @include transition-delay(60ms);
        @include transition-duration(200ms);
        box-shadow: 1px 1px 35px 1px rgba($color: #000000, $alpha: 0.08);

        @include small-devices-and-up {
            img {
                @include transform(scale(1.08));
                @include transition-property(all);
                @include transition-delay(100ms);
                @include transition-duration(200ms);
                @include transition-timing-function(ease-in-out);
            }
        }
        .card-footer {
            //@include transform(translate3d(0,0,0));
            opacity: 1;
            border-radius: 8px;
            @include transition-property(opacity);
            @include transition-delay(80ms);
            @include transition-duration(200ms);

            h1, ul {
                opacity: 1;
                @include transition-property(opacity);
                @include transition-delay(70ms);
                @include transition-duration(100ms);
                @include transition-timing-function(ease-in-out);
            }
        }
    }//hover

    img {
        width: 100%;
    }
    .ico {
        position: absolute;
        top: 2px;
        right: 2px;
        bottom: 2px;
        left: 2px;
        font-size: 164px;
        line-height: 350px;
        color: $text-color;
    }
    .card-footer {
        display: block;
        position: relative;
        padding: 30px;

        @include small-devices-and-up {
            display: block;
            position: absolute;
            right: 0;
            bottom: 0;
            left: 0;
            top: 0;
            background-color: rgba($color: #ffffff, $alpha: 0.96);
            z-index: 2;
            opacity: 0;
            //@include transform(translate3d(0,100%,0));
            @include transition-property(opacity);
            @include transition-delay(60ms);
            @include transition-duration(100ms);
            @include transition-timing-function(ease-out);
        }

        h1, ul {
            opacity: 1;
            
            @include small-devices-and-up {
                opacity: 0;
                @include transition-property(opacity);
                @include transition-delay(60ms);
                @include transition-duration(100ms);
                @include transition-timing-function(ease-in-out);
            }
        }

        h1 {
            font-size: 18px;
            margin-top: 0;
        }

        ul {
            margin: 0;
            padding: 0;
            list-style-type: none;

            li {
                padding: 5px 0;;
            }
        }
    }
}//card