.wrapper--gradient {
    background-color: $body-color;
    @include gradient($deepPurple, $steelBlue, -180deg);

    &.wrapper--info {
        will-change: auto;
        @include flexBox;
        @include flexDirection(column);
        @include flexJustifyContent(center);
        @include flexAlignItems(center);
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        padding: 45px;
        z-index: 1;
        @include transition-duration(370ms);
        @include transition-timing-function(ease-out);

        &:before {
            content: "";
            display: block;
            position: absolute;
            width: 100%;
            height: 100%;
            background-color:$samples-bg-color ;
            opacity: 0;
            pointer-events: none;
            z-index: 0;
        }
    }//wrapper-info

}//gradient-wrapper

.wrapper-samples {
    @include flexBox;
    @include flexDirection(row);
    @include flexJustifyContent(end);
    flex-wrap: wrap;
    position: absolute;
    background-color: $samples-bg-color;
    margin: 0;
    left: 0;
    right: 0;
    padding: 90px 10px 10px 10px;
    opacity: 1;

    @include small-devices-and-up {
        margin: 0 0 0 $side-bar-width;  
        padding: 10px;
    }
}//wrapper-samples

[data-app-state="active"] {
    .wrapper--gradient {
        @include small-devices-and-up {
            &:before {
                opacity: 1;
                @include transition-property(opacity);
                @include transition-delay(700ms);
                @include transition-duration(300ms);
                @include transition-timing-function(ease-out);  
            }
        }
        
    }
    .wrapper--info {
        position: fixed;
        height: 50px;
        width: 100%;
        padding: 0 45px;
        @include transition-property(height);
        @include transition-delay(400ms);
        box-shadow: 1px 1px 10px 1px rgba(0, 0, 0, 0.15);

        @include small-devices-and-up {
            width: $side-bar-width;
            height: 100%;
            padding: 45px;
            @include transition-delay(500ms);
            @include transition-property(width);
            box-shadow: 1px 1px 30px 1px rgba($color: #000000, $alpha: 0.00);
        }
    }//wrapper-info
    .wrapper-samples {
        opacity: 1;
        @include transition-property(opacity);
        @include transition-delay(800ms); 
        @include transition-duration(400ms);
        @include transition-timing-function(ease-in); 
    }
}//data app active
[data-app-state="sleeping"] {
    .wrapper--gradient {
        &:before {
            opacity: 0;
            @include transition-property(opacity);
            @include transition-delay(200ms);
            @include transition-duration(200ms);
            @include transition-timing-function(ease-out);  
        }
    }
    .wrapper--info {
        @include transition-delay(550ms);
        box-shadow: none;
    }
    .wrapper-samples {
        opacity: 0;
        @include transition-property(opacity);
        @include transition-delay(300ms); 
        @include transition-duration(300ms);
        @include transition-timing-function(ease-in);
    }
}//data app sleeping
[data-modal-state="active"] {
    .wrapper-samples,
    .wrapper--info {
        filter: blur(13px);
        -webkit-filter: blur(13px);
        -moz-filter: blur(12px);
        -o-filter: blur(12px);
        -ms-filter: blur(13px);
    }
}//data modal active