@mixin gradient($startColor, $endColor, $deg) {
    /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/$startColor+0,7db9e8+100 */
    background: $startColor; /* Old browsers */
    background: -moz-linear-gradient($deg, $startColor 0%, $endColor 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient($deg, $startColor 0%,$endColor 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient($deg, $startColor 0%,$endColor 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='$startColor', endColorstr='$endColor',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
}