.button-primary {
    @include flexBox();
    @include flexJustifyContent(center);
    @include flexAlignItems(center);
    will-change: auto;
    position: relative;
    background-color: transparentize($color: $yellow, $amount: 0.0);
    border : {
        color: $yellow;
        width: 0;
        style: solid;
        radius: 20px;
    }
    font-size: $font-size-base;
    font-family: 'Yantramanav', sans-serif;
    padding: 10px 20px;
    overflow: hidden;
    color: $deepPurple;
    box-shadow: 2px 2px 15px rgba(0, 0, 0, 0.17);
    @include transition-property(background-color, transform);
    @include transition-duration(200ms);
    @include transition-timing-function(ease-out);
    @include transition-delay(0);

    span {
        position: relative;
        z-index: 2;
    }
    &:after {
        content: "";
        display: block;
        position: absolute;
        top: -1px;
        width: 105%;
        height: 105%;
        background-color: transparent;
        border : {
            color: lighten(white, 45%);
            width: 1px;
            style: solid;
            radius: 20px;
        }
        @include transition(all, 200ms, ease-in);
        right: -1px;
        opacity: .5;
    }
   
    &:hover {
        cursor: pointer; 
        overflow: visible;
        color: $deepPurple;
        background-color: darken($color: $yellow,$amount: 20%);
        box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.17);
        @include transition-property(background-color transform);
        @include transition-duration(200ms);
        @include transition-timing-function(ease-out);
        @include transition-delay(0);
        @include transform(scale(.94));

        &:after {
            opacity: 0;
            border : {
                color: lighten(white, 45%);
                width: 1px;
                style: solid;
                radius: 20px;
            }
            @include transform(scale(1.35));
            @include transition-property(transform opacity);
            @include transition-duration(500ms, 400ms);
            @include transition-timing-function(ease-out);
            @include transition-delay(0);
        }
      
    }//hover

    &.close {
        display: block;
        position: absolute;
        height: 24px;
        width: 64px;
        top: 13px;
        right: 41px;
        margin: 0;
        padding: 0;
        line-height: 24px;
        font-size: 14px;
        text-decoration: none;
        text-align: center;

        @include small-devices-and-up {
            display: none;
        }
    }//close
}

.button-close {
    display: block;
    position: absolute;
    background-color: transparent;
    width: 32px;
    height: 32px;
    font-size: 16px;
    text-align: center;
    line-height: 32px;
    border : {
        style: solid;
        weight: 1px;
        color: transparent;
        radius: 100%
    }
    padding: 0;

    &:hover {
        background-color: #f5f5f5;
    }

    @include small-devices-and-up {
        width: 64px;
        height: 64px;
        font-size: 32px;
        line-height: 64px;
    }
}//button-close