@import url('https://fonts.googleapis.com/css?family=Yantramanav:300,700');

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: 'Yantramanav', sans-serif;
    font-weight: 700;
    color: $text-color;
    line-height: $font-line-height;
}

p,
a,
ol,
ul,
.sample-caption {
    font-family: 'Yantramanav', sans-serif;
    font-weight: 300;
    font-size: $font-size-base;
    color: $text-color;
    line-height: $font-line-height;
}
.sample-caption {
    text-align: center;
    margin-bottom: 50px;
}
.link-download {
    text-decoration: none;
    padding: 5px 10px;
    border : {
        width: 1px;
        style: solid;
        color: $deepPurple;
        radius: 3px;
    }
    margin-left: 10px;
    box-shadow: 1px 1px 5px 1px rgba($color: #000000, $alpha: 0.04);
    @include transition-property(all);
    @include transition-duration(300ms);
    @include transition-timing-function(ease-in-out);

    &:hover {
        background-color: $deepPurple;
        color: white;
        box-shadow: 2px 2px 8px 1px rgba($color: #000000, $alpha: 0.1);

        .icon {
            font-size: 20px;
        }
    }

    .icn {
        display: inline-block;
        position: relative;
        top: 3px;
        margin-left: 5px;
        padding-left: 5px;
        border-left: 1px solid $deepPurple;
        font-size: 16px;
    }
}
@each $header in $headers {
    @include small-devices-and-up {
        #{$header} {
            $font-ratio: $font-ratio + 1;
            font-size: ($font-size-base + 10px) * $font-ratio;
        } 
    }
}