//Typography
$font-size-base: 14px;
$font-line-height: 150%;
$font-ratio: 1.61;
$headers: h6 h5 h4 h3 h2 h1;

//Transitions
$transition-duration-base: 175ms;
$transition-timing-base: ease-out;
$transition-timing-bezier: cubic-bezier(0.655, 0.270, 0.355, 1.130);

//Resolutions
$small-devices: 768px;
$large-devices: 1742px;

//Layout
$side-bar-width: 380px;