.flex-grid {
    @include flexBox;
    @include flexDirection(row);
    @include flexJustifyContent(end);
    flex-wrap: wrap;

    .grid-item{
        width: 145px;
        text-align: center;
        margin: 20px;

        @include small-devices-and-up {
            width: 200px;
        }

        span {
            font-size: 96px;
            color: $text-color;
        }
    }
}