.wrapper--splash-screen {
    will-change: auto;
    @include flexBox;
    @include flexJustifyContent(flex-start);
    @include flexAlignItems(flex-start);
    @include flexDirection(column);
    height: initial;
    z-index: 3;

    @include small-devices-and-up {
        @include flexJustifyContent(space-between);
        @include flexAlignItems(center);
    }
    .my-icon {
        width: 70px;
        margin-bottom: 40px;
        position: relative;
        top: 25%;
        left: 0;
        @include transition-property(all);
        @include transition-delay(400ms);
        @include transition-duration(350ms);
        @include transition-timing-function(ease-in-out);

        @include small-devices-and-up {
            width: 75px;
            height: 75px;
            margin-bottom: 40px;
            position: relative;
            top: auto;
            left: auto;
        }

        .my-icon--bug {
            fill: lighten($color: $deepPurple, $amount: 20%);
        }
    }//my-icon
    .header--site-title {
        will-change: auto;
        position: relative;
        will-change: auto;
        margin: 0;
        font-size: 32px;
        text-transform: uppercase;
        z-index: 3;
        color: $yellow;
        @include transition-property(all);
        @include transition-delay(200ms);
        @include transition-duration(450ms);
        @include transition-timing-function(ease-in-out);
    }
    .header--sub-title {
        will-change: auto;
        color: $yellow;
        position: relative;
        margin: 0;
        z-index: 3;
    }//header--sub-title
    .button-primary {
        margin-top: 2em;
        z-index: 3;

        @include small-devices-and-up {
            margin-top: 0;
        }
    }//button-primary
}//wrapper-splash-screen

[data-app-state="active"] {
    .wrapper--splash-screen {
        margin-top: 13px;
        @include transition-property(margin-top);
        @include transition-duration(300ms);
        @include transition-delay(600ms);
        @include small-devices-and-up {
            margin-top: 0;
        } 

    }//wrapper--splash-screen
    .my-icon {
        top: -3px;
        width: 45px;
        @include transition-property(all);
        @include transition-delay(500ms);
        @include transition-duration(250ms);
        @include transition-timing-function(ease-in-out);

        @include small-devices-and-up {
            width: 75px;
            height: 75px;
            margin-bottom: 40px;
            position: relative;
            top: auto;
            left: auto;
        }
    }
    .header--site-title {
        opacity: 0;
        @include transition-property(all);
        @include transition-delay(300ms);
        @include transition-duration(150ms);
        @include transition-timing-function(ease-in-out);
    }//header--site-title
    .header--sub-title {
        opacity: 0;
        @include transition-property(opacity);
        @include transition-delay(200ms);
        @include transition-duration(200ms);
        @include transition-timing-function(ease-out);
    }//header--sub-title
    .button-primary {
        .active {
            display: block;
        }
        .sleeping {
            display: none;
        }
        &.close {
            opacity: 1;
            @include transition-property(opacity);
            @include transition-delay(400ms);
            @include transition-duration(200ms);
            @include transition-timing-function(ease-in-out);
        }
        &.toggle {
            opacity: 0;
            background-color: #65568b;
            color: white;
            border: none;

            @include small-devices-and-up {
                opacity: 1;
            }
        }
    }//button primary
}//data app active

[data-app-state="sleeping"] {
    .wrapper--splash-screen {
        margin-top: 0;
        @include transition-property(margin-top);
        @include transition-duration(400ms);
        @include transition-delay(600ms);
    }//wrapper--splash-screen
    .header--site-title {
        @include transition-delay(950ms);
    }//header--site-title
    .header--sub-title {
        @include transition-property(opacity);
        @include transition-delay(1100ms);
        @include transition-duration(1100ms);
        @include transition-timing-function(ease-in-out);
    }//header--sub-title 
    .button-primary {
        .active {
            display: none;
        }
        .sleeping {
            display: block;
        }
        &.close {
            opacity: 0;
        }
        &.toggle {
            @include transition-property(opacity, background-color);
            @include transition-delay(800ms, 300ms);
            @include transition-duration(350ms);
            @include transition-timing-function(ease-out);
        }
    }//button-primary
}//data app sleeping