@font-face {
    font-family: 'gsl-icons';
    src:  url('../fonts/gsl/gsl-icons.eot?j514kb');
    src:  url('../fonts/gsl/gsl-icons.eot?j514kb#iefix') format('embedded-opentype'),
      url('../fonts/gsl/gsl-icons.ttf?j514kb') format('truetype'),
      url('../fonts/gsl/gsl-icons.woff?j514kb') format('woff'),
      url('../fonts/gsl/gsl-icons.svg?j514kb#gsl-icons') format('svg');
    font-weight: normal;
    font-style: normal;
  }
  
  [class^="gsl-"], [class*=" gsl-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'gsl-icons' !important;
    //speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
  
    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
