@mixin transition(
    $objects: all,
    $duration: $transition-duration-base,
    $timingFunction: $transition-timing-base
) {
    transition: $objects $duration $timingFunction;
    -ms-transition: $objects $duration $timingFunction;
    -webkit-transition: $objects $duration $timingFunction;
    -o-transition: $objects $duration $timingFunction;
    -moz-transition: $objects $duration $timingFunction;
}
@mixin transition-delay($delay...) {
    transition-delay: $delay;
    -ms-transition-delay: $delay;
    -webkit-transition-delay: $delay;
    -o-transition-delay: $delay;
    -moz-transition-delay: $delay;
}
@mixin transition-property($property...) {
    transition-property: $property;
    -ms-transition-property: $property;
    -webkit-transition-property: $property;
    -o-transition-property: $property;
    -moz-transition-property: $property;
}
@mixin transition-timing-function($timing-function) {
    transition-timing-function: $timing-function;
    -ms-transition-timing-function: $timing-function;
    -webkit-transition-timing-function: $timing-function;
    -o-transition-timing-function: $timing-function;
    -moz-transition-timing-function: $timing-function;
}
@mixin transition-duration($duration...) {
    transition-duration: $duration;
    -ms-transition-duration: $duration;
    -webkit-transition-duration: $duration;
    -o-transition-duration: $duration;
    -moz-transition-duration: $duration;
}
